.recharts-tooltip-wrapper .custom-tooltip {
    margin: 0;
    line-height: 24px;
    border: 1px solid #f5f5f5;
    background-color: hsla(0, 0%, 100%, .9);
    padding: 10px
}

.recharts-tooltip-wrapper .custom-tooltip .label {
    margin: 0;
    color: #666;
    font-weight: 600
}

.recharts-tooltip-wrapper .custom-tooltip .intro {
    border-top: 1px solid #f5f5f5;
    margin: 0;
    color: #666;
    font-weight: 600
}

.recharts-tooltip-wrapper .custom-tooltip .asreview {
    margin: 0;
    color: #FFCD00;
    font-weight: 400
}

.recharts-tooltip-wrapper .custom-tooltip .random {
    margin: 0;
    color: #000000;
    font-weight: 400
}
